.Home-main-container-data{
    width: 95%;
    height: 100%;
    background-color: rgb(213, 132, 132);
}
.Home-main-container-hedaing{
    width: 100%;
    height: 70px;
    background-color: coral;
    display: flex;
    align-items: center;
    justify-content: center;
    border-bottom: 2px dotted rgb(0, 255, 21);
}
.Home-main-containerbox{
    width: 47%;
    height: 80%;
    margin:5px;
    display: flex;
    align-items: center;
    justify-content: center;
}
.homefollowing-link-box{
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    text-decoration: none;
    color: rgb(0, 255, 0);
    font-size: 17px;
    border-radius: 5px;
    border: 1px solid rgb(255, 255, 255);
    font-weight: 600;
    transition: 1.5s;



}
.homefollowing-link-box:hover{
background-color:rgb(0, 95, 0);
border-radius: 20px;
border: 1px solid rgb(0, 126, 34);
color: rgb(255, 255, 255);


}