.Main-container {
  margin-top: 60px;
  width: 100%;
  height: 100vh;
  background-color: crimson;
  color: white;
  display: flex;
  align-items: center;
  justify-content: center;
}
.mainside-left-container {
  width: 300px;
  height: 100vh;

  display: flex;
  align-items: center;
  justify-content: center;
}
.mainside-middile-container {
  width: 750px;
  height: 100vh;

  display: flex;
  align-items: center;
  justify-content: center;
}
.mainside-right-container {
  width: 300px;
  height: 100vh;

  display: flex;
  align-items: center;
  justify-content: center;
}
.mainside-left-box-container {
  width: 90%;
  height: 100%;
}
.navlink-box {
  width: 100%;
  height: 50px;
  border: 1px solid rgb(255, 255, 255);
  display: flex;
  align-items: center;
  border-radius: 10px;
  cursor: pointer;
  margin-bottom: 10px;
  transition: 1s;
  text-decoration: none;
  color: white;
}
.navlink-box:hover {
  width: 100%;
  height: 50px;
  display: flex;
  align-items: center;
  color: rgb(255, 106, 0);
  background-color: white;
  transition: 1s;
}
.topboxhome {
  margin-top: 30px;
}
.iconhomeside {
  margin-left: 10px;
  margin-right: 10px;
  font-size: 25px;
}
.lasthomeuser{
    display: flex;
    align-items: center;
    justify-content: center;
}
.user-details-boxs{
  width: 45px;
  height: 45px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: white;
  border-radius: 50%;
color: black;
}
.user-details-name-boxs{
  width: 210px;
  height: 100%;
  margin-left: 5px;
}
.user-details-name{
  margin: 0;
  font-size: 16px;
  font-weight: 600;
}
.user-details-username{
  margin: 0;
  font-size: 16px;
  font-weight: 600;
}