.main-user-twite-details {
  width: 100%;
  height: 90vh;
  overflow-x: hidden;
  overflow-y: scroll;
  cursor: default;
}
.message-show-box-container {
  width: 95%;
  height: auto;
  background-color: rgb(255, 255, 255);
  margin: 20px;
  border-radius: 10px;
  color: black;
  display: flex;
}
.sideprofile-image-container {
  width: 70px;
  height: 100%;
  margin-bottom: 10px;
  display: flex;
  align-items: flex-start;
  justify-content: center;
}
.sideprofile-image {
  width: 50px;
  height: 50px;
  background-color: coral;
  border-radius: 50%;
  margin-top: 10px;
  text-decoration: none;
}
.userdetails-username-more-container {
  width: 90%;
  height: 100%;
}
.userdetails-username-more-box {
  width: 100%;
  height: 40px;
  display: flex;
  align-items: flex-end;
  margin-top: 10px;
}
.containerthreedot {
  position: relative;
  right: 0px;
  width: 90%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: flex-end;
}
.threedot {
  margin-right: 10px;
  font-weight: bolder;
  font-size: 26px;
  cursor: pointer;
}
.username-show {
  font-weight: 700;

}
.navlink{
    color: black;
  text-decoration: none;
}
.navlink:hover{
    color: black;
  text-decoration: underline;
}

.message-user-container{
    width: 100%;
    height: 100%;
}
.message-user{
    font-weight: 600;
   text-align: justify;
   margin-right:30px ;
}
.footer-box-container{
    width: 100%;
    height: 40px;
    display: flex;
    align-items: center;
}

.comment-post-container,.like-post-container,.share-post-container,.view-post-container{
    width: 100px;
    height: 100%;
    display: flex;
    align-items: center;
    margin-left: 55px;
}
.first{
    margin-left: 0px;
}
.iconsend{
    cursor: pointer;
}
.number-show{
    margin-left: 10px;
}