.postinputbox-container {
  width: 100%;
  height: auto;
  background-color: rgb(255, 255, 255);
  display: flex;
  align-items: center;
  justify-content: center;
}
.postinputbox-side-container {
  width: 60px;
  height:150px;
  display: flex;
  align-items: flex-start;
  justify-content: center;
}
.user-details-box {
  margin-top: 10px;
  width: 40px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  background-color: white;
  text-decoration: none;
  cursor: pointer;
}
.icon-user {
  font-size: 30px;
}

.postinputbox-left-container {
  width: 92%;
  height: 100%;
  background-color: white;
}
.postinputbox-left-top-container {
  width: 100%;
  height: 60%;
  background-color: rgb(255, 255, 255);
  display: flex;
  align-items: center;
  justify-content: center;
}
.postinputbox-left-bottom-container {
  width: 100%;
  height: 60px;
  background-color: rgb(255, 255, 255);
  display: flex;
  align-items: center;
  justify-content: flex-end;
}
.button-container-box {
  margin-right: 30px;
  padding: 10px;
  width: 100px;
  border: 1px solid rgb(211, 211, 211);
  border-radius: 10px;
  cursor: pointer;
  background-color: green;
  color: white;
  transition: 1.5s;
}
.button-container-box:hover {
  background-color: rgb(0, 166, 255);
  color: white;
}
#message-box-input {
  margin-top: 20px;
  width: 100%;
  height: 70%;
  padding: 8px;
  border: none;
  outline:none;
  font-size: 25px;
  font-weight: 700;
  color: black;
  resize: none;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  overflow-y: scroll;
}
