.loader-home-home-container {
    width:100%;
    height: 90vh;
    background-color: white;
  }
  
  .loader {
    border: 7px solid rgba(204, 204, 204, 0.3);
    border-top: 7px solid #ff00d9;
    border-left: 7px solid #ff00d9;
    border-right: 7px solid #ff00d9;
    border-radius: 50%;
    width: 40px;
    height: 40px;
    animation: spin 2s linear infinite;
  }
  .loader-con{
    width: 100%;
    height: 50%;
    display: flex;
    justify-content: center;
    align-items: flex-end;
  }
  .heading-con{
    width: 100%;
    height: 80px;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  @keyframes spin {
    0% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(360deg);
    }
  }
  